import React from 'react';
import s from './styles.module.scss';
import { useTranslation } from "react-i18next";
import AnchorLink from 'react-anchor-link-smooth-scroll'

const BurgerMenu = ({ isOpen }) => {
    const { t } = useTranslation("intro");

    return (
        <div className={isOpen ? s.burgerMenu + ' ' + s.burgerAppear : s.burgerMenu}>
            <nav className={s.burgerMenu__nav}>
                <AnchorLink href={'#intro'} offset={5}>{ t('burgerMenu', { returnObjects: true }).home }</AnchorLink>
                <AnchorLink href={'#about'} offset={0}>{ t('burgerMenu', { returnObjects: true }).about }</AnchorLink>
                    {/* <AnchorLink href={'#projects'} offset={50}>Projects</AnchorLink> */}
                <AnchorLink href={'#video'} offset={0}>{ t('burgerMenu', { returnObjects: true }).contact }</AnchorLink>
            </nav>
            {/* <div className={s.burgerMenu__mail}>
                <p><a href={'mailto:masha.mashianova1@gmail.com'}>masha.mashianova1@gmail.com</a></p>
            </div> */}
        </div>
    )
}

export default BurgerMenu;