import React from 'react';
import s from './styles.module.scss';
import handleViewport from 'react-in-viewport';
import AnchorLink from 'react-anchor-link-smooth-scroll'

import { MailBox, PhoneIcon, PlayIcon, Github, UpArrow } from '../../../assets/icons';
import VideoEng from '../../../assets/videos/about_me_eng.mp4';
import VideoRus from '../../../assets/videos/about_me_rus.mp4';

const Video = ({ lang, inViewport, ...props }) => {

    const { forwardedRef } = props;

    const startVideo = () => {
        let video = document.querySelector("[name='video']")
        video.play(); 
        document.querySelector("[name='custom_play-btn']").style.display = "none"
        video.controls = true
    }

    return (
        <div className={s.video__wrap_video} ref={forwardedRef}>
            {
                lang === 'eng' ? 
                    <video crossOrigin={""} name={"video"} preload={"none"}>
                        <source src={VideoEng} type="video/mp4"></source>
                    </video> :
                        <video crossOrigin={""} name={"video"} preload={"none"}>
                            <source src={VideoRus} type="video/mp4"></source>
                        </video>
            }
            <div name={"custom_play-btn"} className={s.video__wrap_videoBtn}>
                <PlayIcon
                    width={60}
                    onClick={startVideo} 
                />
            </div>
        </div>
    )
}

const ViewportBlock = handleViewport(Video);


const VideoAndContacts = ({ lang, ...props }) => {

    const pauseVideo = () => {
        let video = document.querySelector("[name='video']")
        video.pause(); 
        video.currentTime = 0;
        document.querySelector("[name='custom_play-btn']").style.display = "flex"
        video.controls = false
    } 

    return (
        <div id={'video'} className={s.video}>
            <div className={s.video__wrap}>
 
                <ViewportBlock onEnterViewport={() => console.log('enter viewport')} onLeaveViewport={pauseVideo} lang={lang} />

                <div className={s.video__wrap_contact}>
                    <p><a href={'https://github.com/asmster666'}>
                        <Github width={40} />
                        <span>github.com/asmster666</span>
                    </a></p>
                    <p><a href={'tel:6281213989947'}>
                        <PhoneIcon id={'phoneIcon'} />
                        <span>+62 812 1398 9947</span>
                    </a></p>
                    <p><a href={'mailto:masha.mashianova1@gmail.com'}>
                        <MailBox id={'mailIcon'} />
                        <span>masha.mashianova1@gmail.com</span>
                    </a></p>
                </div>
            </div>

            <div className={s.video__btnToTop}>
                <AnchorLink href={'#intro'} offset={5}><UpArrow width={22} /></AnchorLink>
            </div>

            <footer className={s.video__footer}>
                <p>Copyright &#169; 2023 Masha M. All rights reserved.</p>
            </footer>
        </div>
    )
}

export default VideoAndContacts;