import React, { useState, useEffect } from 'react';
import s from './styles.module.scss';

import { useTranslation } from 'react-i18next';

import { BurgerIcon, BurgerArrow } from '../../../assets/icons';
import { LanguageToggle } from '../../../elements';

const Header = ({ openFn, open }) => {

    const { i18n } = useTranslation();

    const [ activeLang, setActiveLang ] = useState(0);

    const handleChangeLang = (lang) => {
        setTimeout(() => {
            i18n.changeLanguage(lang)
            sessionStorage.setItem("lang", lang)
            window.location.reload();
        }, 500)
        lang === 'eng' ? setActiveLang(1) : setActiveLang(2)
    };

    useEffect(() => {
        if(sessionStorage.getItem("lang") === null) {
            sessionStorage.setItem("lang", "eng")
            setActiveLang(1)
        } else {
            sessionStorage.getItem("lang") === 'eng' ? setActiveLang(1) : setActiveLang(2)
        }
    }, [])

    return (
        <header className={s.header}>
            <h1 className={s.header__name}><b>Masha Mashianova</b></h1>
            <div className={s.header__btnWrap}> 
                <LanguageToggle activeL={activeLang} changeLangFn={handleChangeLang} closeBurger={openFn} isBurgerOpen={open} />
                {
                    !open ?
                        <BurgerIcon 
                            className={s.header__btnWrap_burgerMenu} 
                            onClick={openFn}
                        /> :
                            <BurgerArrow 
                                className={s.header__btnWrap_burgerMenu}
                                onClick={openFn}
                            />
                }
            </div>
        </header>
    )
}

export default Header;